import { post, get, put, _delete } from "@/lin/plugin/axios";

class Setting {
    async getSettingByKey(key) {
        const res = await get(`cms/settings/key/${key}`);
        return res;
    }
    async getSettingByKeys(params) {
        const res = await get(`cms/settings/keys`, params);
        return res;
    }
    async setSettingValues(params) {
        const res = await post(`cms/settings/set-values`, params);
        return res;
    }

    async createShareCode() {
        const res = await get(`cms/share/createcode`);
        return res;
    }

    async getCode() {
        const res = await get(`cms/share/getcode`);
        return res;
    }
    async queryBindCode() {
        const res = await get(`cms/share/querybindcode`);
        return res;
    }

    async bindCode(shareCode) {
        const res = await get(`cms/share/bindcode`, { ShareCode: shareCode });
        return res;
    }

    async getPagedList(params) {
        const res = await get(`cms/share/query`, params);
        return res;
    }
}

export default new Setting();
